div.modalContainerAll {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(100, 100, 100, 0.9);
  z-index: 10;
}

div.modalContainerAll div.modalContainer {
  width: 50%;
  height: 50%;
  padding: 10px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto 1fr;
  gap: 10px;
  border: 1px solid black;
  background-color: white;
  border-radius: 4px;
  box-shadow: 0 0 4px royalblue;
}

div.modalContainerAll div.modalContainer div.Header {
  display: flex;
  justify-content: space-between;
  padding: 6px 10px;
  border-radius: 4px;
  border-bottom: 1px solid black;
}

div.modalContainerAll div.modalContainer div.Header button {
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  border: 1px solid black;
  background: none;
}

div.modalContainerAll div.modalContainer div.Main {
  flex: 1;
}
