aside.ToolBoxContainer {
  max-width: 300px;
  position: absolute;
  top: 20px;
  right: 5px;
  padding: 5px;
  border: 1px solid black;
  border-radius: 2px;
}

aside.ToolBoxContainer div.ToolBoxHeader {
  display: flex;
  border-bottom: 1px solid black;
}

@media only screen and (max-width: 600px) {
  aside.ToolBoxContainer div.ToolBoxHeader h2 {
    writing-mode: vertical-rl;
    text-orientation: sideways-right;
  }
}

aside.ToolBoxContainer div.ToolBoxMain {
  display: flex;
  flex-direction: column;
}

aside.ToolBoxContainer div.ToolBoxMain div.StrokeFillContainer {
  display: flex;
  flex-direction: column;
}

aside.ToolBoxContainer div.ToolBoxMain label {
  display: flex;
  align-items: center;
}
